<template>
  <div>
    <div class="searchs">
      <el-form :inline="true" @submit.native.prevent="onSubmit" :model="searchs" class="form-search">
        <el-row :gutter="24">
          <el-col>
            <el-form-item label="标题">
              <el-input v-model="searchs.keyword_search_value" placeholder="标题"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" native-type="submit">查询</el-button>
              <router-link to="/article/focuscreate">
                <el-button style="margin-left: 8px;" type="success">创建</el-button>
              </router-link>
            </el-form-item>
          </el-col>
          <!-- <el-col>
                      <el-form-item label="交易日期">
                        <el-date-picker
                          v-model="addtime"
                          type="daterange"
                          align="right"
                          unlink-panels
                          range-separator="至"
                          start-placeholder="开始日期"
                          end-placeholder="结束日期"
                          :picker-options="pickerOptions"
                        />
                      </el-form-item>
                      <el-form-item>
                        <el-button type="primary" native-type="submit">查询</el-button>
                      </el-form-item>
                    </el-col> -->
        </el-row>
      </el-form>
    </div>
    <el-table :data="tableData" class="list" v-loading="loading">
      <el-table-column prop="id" label="编号" align="center" width="80" />
      <el-table-column prop="title" label="文章标题" align="center" width="240" />
      <el-table-column prop="cat_name" label="文章分类" align="center" width="90" />
      <el-table-column prop="cat_type_name" label="文章类型" align="center" />
      <el-table-column prop="sort" label="排序" align="center" />
      <el-table-column prop="is_show_index" label="是否显示在首页" align="center" width="50">
        <template slot-scope="scope">
          <span>{{ scope.row.is_show_index === 'Y' ? '是' : '否' }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="url" label="文章链接" align="center" width="180px" />
      <el-table-column prop="uptime_date" label="更新时间" align="center" />
      <el-table-column prop="addtime_date" label="添加时间" align="center" />
      <el-table-column prop="address" align="center" label="操作" width="150">
        <template slot-scope="scope">
          <el-button @click="editItem(scope.row)" type="primary" size="small">编辑</el-button>
          <el-button @click="removeItem(scope.row)" type="danger" size="small">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      layout="total, prev, pager, next"
      :total="count"
      :current-page="page_curren"
      :hide-on-single-page="false"
      @current-change="currentChange"
      class="pagination"
    />
    <el-dialog title="会员详情" :visible.sync="dialogFormVisible">
      <el-form :model="detail">
        <el-form-item label="活动名称" label-width="80">
          <el-input v-model="detail" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { Message } from 'element-ui'
import Fetch from '../../utils/fetch'
import Fetch2 from '../../utils/fetch2'
import Fetch3 from '../../utils/fetch3'
import Fetch4 from '../../utils/fetch4'

export default {
  data() {
    return {
      dialogFormVisible: false,
      searchs: {
        keyword_search_value: '',
        // begin_addtime: '',
        // end_addtime: '',
        cat_id: '',
      },
      config: {
        cats: {},
      },
      loading: false,
      detail: {},
      all_money: '',
      tableData: [],
      count: 0,
      page_curren: 1,
      addtime: [],
      pickerOptions: {
        shortcuts: [
          {
            text: '本月',
            onClick(picker) {
              picker.$emit('pick', [new Date(), new Date()])
            },
          },
          {
            text: '今年至今',
            onClick(picker) {
              const end = new Date()
              const start = new Date(new Date().getFullYear(), 0)
              picker.$emit('pick', [start, end])
            },
          },
          {
            text: '最近六个月',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setMonth(start.getMonth() - 6)
              picker.$emit('pick', [start, end])
            },
          },
        ],
      },
    }
  },
  // watch: {
  //   addtime(val) {
  //     if(val) {
  //       this.searchs.begin_addtime = dayjs(val[0]).format('YYYY-MM-DD')
  //       this.searchs.end_addtime = dayjs(val[1]).format('YYYY-MM-DD')
  //     } else {
  //       this.searchs.begin_addtime = ''
  //       this.searchs.end_addtime = ''
  //     }
  //   }
  // },
  mounted() {
    this.FetchList()
  },
  methods: {
    FetchList() {
      this.loading = true
      Fetch('/article/art/getlist', {
        page_curren: this.page_curren,
        ...this.searchs,
        type: 'focus',
      })
        .then(r => {
          this.tableData = r.data.list
          this.count = r.data.count
          this.page_curren = Math.floor(r.data.page_curren)
        })
        .catch(res => {
          Message.error(res.msg)
        })
      this.loading = false
    },
    currentChange(page) {
      this.page_curren = page
      this.FetchList()
    },
    onSubmit() {
      this.page_curren = 1
      this.FetchList()
    },
    editItem(row) {
      this.$router.push('/article/focusedit/' + row.id)
    },
    removeItem(row) {
      this.$confirm('是否确认删除此条数据?', undefined, {
        type: 'warning',
        beforeClose: (action, instance, done) => {
          if (action === 'confirm') {
            instance.confirmButtonLoading = true
            Fetch4('/article/art/delete/' + row.id)
              .then(() => {
                instance.confirmButtonLoading = false
                Message.success('删除成功')
                done()
                this.FetchList()
              })
              .catch(() => {
                instance.confirmButtonLoading = false
                Message.error('删除失败')
              })
          } else {
            done()
          }
        },
      })
    },
  },
}
</script>
